    html,body {
        height: 100vh;
        height: calc(var(--vh, 1vh) * 100);
        background-color: #F8F9FA;
        font-family: 'Poppins', sans-serif; /* Apply Poppins font to all elements */
    }

    .card {
        margin: 20px;
        box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1); 
        border: none; 
        border-radius: 15px;
    }

    .chat-container {
        max-height: calc(100vh - 70px); /* Adjusted based on the height of the navigation */
        overflow-y: auto;
        padding: 10px;
    }

    .chat-bubble {
        max-width: 100%;
        margin: 10px;
        padding: 10px;
        border-radius: 10px;
        font-family: 'Poppins', sans-serif; /* Apply Poppins font to chat bubbles */
    }

    .custom-green-button:hover {
        background-color: darkgreen;
    }

    .user-bubble {
        background-color: #9300FF;
        color: #fff;
        align-self: flex-end; /* Align user's messages to the right */
    }
  
    .response-bubble {
        background-color: #f0f0f0;
        color: #333;
        align-self: flex-start; /* Align Thuto's responses to the left */
    }

    .input-class{
        border: none; border-radius: 30px;
        background-color: white;
        color: #8C969E;
        padding: 20px; flex: 1;
        box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1); 
    }

    .action-button{
        border-radius: 30px;
        margin-left: 10px;
        padding: 20px;
        background-color: rgb(24, 23, 23);
        color: #f0f0f0;
        
    }

    .container-width{
        display: flex; flex: 1;
    }


    .benefit-cta {
    background: #837cfe;
    color: #fff;
    border: 1px solid #fff;
    padding: 10px 20px;
    font-size: 20px;
    /* margin-top: 20px; */
    display: inline-block;
    text-decoration: none;
    border-radius: 15px; /* Adding rounded corners */
}